.paralax-background{
  background-color: blue;
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.homepage .content-container{
  padding-top: 550px;
}
.homepage .content{
  position: relative;
  padding-top: 50px;
  padding-right: 15px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
}
#name{
  margin: 0px;
}
.profile{
  position: absolute;
  top:-100px;
  width: 225px;
  display: flex;
  align-items: center;
  flex-flow: column;
  height: 100%;
}
.prof-pic{
  width: 100%;
  height: auto;
}
.sidebar{
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-flow: column;
  padding-left: 15px;
}
.sticky-sidebar{
  position: sticky; 
  top: 0px;
}
.homepage .content{
  background-color: white;
}

.main{
  margin-left: 225px;
  padding-left: 15px;
}